import React from "react";
import PropTypes from "prop-types";
import T from "../i18n/translate.js";
import Player from "../Player";
import ScorerContainer from "../Scorer/ScorerContainer.js";
import "./PlayerList.css";
import SapoMatchControls from "../Scorer/SapoMatchControls.js";

function PlayerList({
  addingPointsPlayerIndex,
  data,
  addPoints,
  addRoundPoints,
  stopAddingPoints,
  addPlayer,
  startOver,
  beginAddingPoints,
  deletePlayer,
  editPlayer,
  endGame,
  gameOver,
  strings
}) {
  return (
    <div>
      {addingPointsPlayerIndex !== null ? (
        <ScorerContainer
          player={data[addingPointsPlayerIndex]}
          addPoints={addPoints}
          addRoundPoints={addRoundPoints}
          stopAddingPoints={stopAddingPoints}
          gameOver={gameOver}
        />
      ) : (
        <SapoMatchControls
          addPlayer={addPlayer}
          startOver={startOver}
          endGame={endGame}
          gameOver={gameOver}
        />
      )}

      {data && addingPointsPlayerIndex === null ? (
        <div className="playerList">
          {data.map((player, i) => (
            <Player
              key={i}
              index={i}
              player={player}
              beginAddingPoints={() => beginAddingPoints(player, i)}
              deletePlayer={() => deletePlayer(player, i)}
              editPlayer={() => editPlayer(player, i)}
              gameOver={gameOver}
            />
          ))}
        </div>
      ) : null}
    </div>
  );
}

PlayerList.propTypes = {
  addPoints: PropTypes.func.isRequired,
  addRoundPoints: PropTypes.func.isRequired,
  stopAddingPoints: PropTypes.func.isRequired,
  startOver: PropTypes.func.isRequired,
  addingPointsPlayerIndex: PropTypes.number,
  data: PropTypes.array,
  addPlayer: PropTypes.func.isRequired,
  beginAddingPoints: PropTypes.func.isRequired,
  editPlayer: PropTypes.func.isRequired,
  deletePlayer: PropTypes.func.isRequired,
  strings: PropTypes.object.isRequired,
  endGame: PropTypes.func.isRequired,
  gameOver: PropTypes.bool.isRequired
};

export default T()(PlayerList);



