import React from "react";
import PropTypes from "prop-types";
import T from "../i18n/translate.js";
import "./Player.css";
import deleteIcon from "../../assets/delete.svg";
import editIcon from "../../assets/edit.svg";

function Player({
  player,
  beginAddingPoints,
  deletePlayer,
  editPlayer,
  gameOver,
  strings,
  index
}) {
  return (
    <div>
      <div className="playerRow">
        {!gameOver ? (
          <span className="playerDelete">
            <img
              src={editIcon}
              className="removeButton"
              onClick={editPlayer}
              title={strings.editPlayer}
              alt={strings.editPlayer}
            />{" "}
            <img
              src={deleteIcon}
              className="removeButton"
              onClick={deletePlayer}
              title={strings.removePlayer}
              alt={strings.removePlayer}
            />
          </span>
        ) : null}
        <span className="playerName">
          {gameOver && index === 0 ? <TrophyEmoji /> : null}
          {player.name}
          {gameOver && index === 0 ? <TrophyEmoji /> : null}
          <br />
          <span className="turnCount">
            {~~player.roundsPlayed}{" "}
            {~~player.roundsPlayed !== 1 ? strings.turns : strings.turn}
          </span>
        </span>
        <span className="playerScore">{player.score} </span>
        {!gameOver ? (
          <span className="playerActions">
            <button type="button" onClick={beginAddingPoints}>
              {strings.add}
            </button>
          </span>
        ) : null}
      </div>

      <div className="playerActions" />
    </div>
  );
}

const TrophyEmoji = () => {
  return (
    <span role="img" aria-label="Trophy">
      
    </span>
  );
};

Player.propTypes = {
  player: PropTypes.shape({
    name: PropTypes.string,
    score: PropTypes.number,
    roundsPlayed: PropTypes.number
  }).isRequired,
  beginAddingPoints: PropTypes.func.isRequired,
  deletePlayer: PropTypes.func.isRequired,
  editPlayer: PropTypes.func.isRequired,
  strings: PropTypes.object.isRequired,
  gameOver: PropTypes.bool.isRequired,
  index: PropTypes.number.isRequired
};

export default T()(Player);



