import React from "react";
import T from "../i18n/translate.js";
import "./SapoMatchControls.css";
import PropTypes from "prop-types";

function SapoMatchControls({
  addPlayer,
  startOver,
  strings,
  endGame,
  gameOver
}) {
  return (
    <div>
      <div className="matchControls">
        <button type="button" onClick={addPlayer}>
          {strings.addPlayer}
        </button>

        {gameOver ? (
          <button type="button" onClick={startOver}>
            {strings.newGame}
          </button>
        ) : null}

        {!gameOver ? (
          <button
            type="button"
            className="endGameBtn"
            onClick={() => {
              if (window.confirm(strings.endGameMessage)) {
                endGame();
              }
            }}
          >
            {strings.endGame}
          </button>
        ) : null}
      </div>
    </div>
  );
}

SapoMatchControls.propTypes = {
  addPlayer: PropTypes.func.isRequired,
  startOver: PropTypes.func.isRequired,
  endGame: PropTypes.func.isRequired,
  gameOver: PropTypes.bool.isRequired
};

export default T()(SapoMatchControls);



