import React, { Component } from "react";
import PropTypes from "prop-types";
import "./App.css";
import SapoScoresContainer from "./components/SapoScoresContainer.js";
import SapoEmoji from "./components/Utility/SapoEmoji.js";
import SapoStorage from "./data/SapoStorage.js";
import SantaBeatrizLogo from "./assets/santabeatriz-logo.png";

class App extends Component {
  constructor() {
    super();

    this.getSelectedLanguageClass = lang => {
      if (lang === this.state.currentLanguage) return "selectedLanguage";

      return "";
    };

    this.changeLanguage = lang => {
      this.setState({ currentLanguage: lang });

      SapoStorage.setLanguage(lang)
        .then(() => {
          console.log("Set default language to:", lang);
        })
        .catch(ex => {
          console.error("Error while saving language selection:", ex);
        });
    };

    this.state = { currentLanguage: null };

    SapoStorage.getLanguage().then(lang => {
      this.changeLanguage(lang);
    });
  }
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h1 className="title">
            <SapoEmoji />{" "}
            <a
              href="http://www.santabeatriz.com"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src={SantaBeatrizLogo}
                alt="Sapo Santa Beatriz"
                style={{
                  width: "50%",
                  maxWidth: "200px",
                  verticalAlign: "middle"
                }}
              />
            </a>{" "}
            <SapoEmoji />
          </h1>
        </div>
        <div className="App-content">
          <SapoScoresContainer currentLanguage={this.state.currentLanguage} />
        </div>
        <div className="App-footer">
          <span
            className={
              "language " +
              (this.state.currentLanguage === "es" ? "selectedLanguage" : null)
            }
            onClick={() => this.changeLanguage("es")}
          >
            Espaol
          </span>
          &nbsp;|&nbsp;
          <span
            className={
              "language " +
              (this.state.currentLanguage === "en" ? "selectedLanguage" : null)
            }
            onClick={() => this.changeLanguage("en")}
          >
            English
          </span>
        </div>
      </div>
    );
  }
  getChildContext() {
    return {
      currentLanguage: this.state.currentLanguage
    };
  }
}

App.childContextTypes = {
  currentLanguage: PropTypes.string
};

export default App;



